 y
 *{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
 }
 :root{
  --rad:0.7rem;
  --dur:0.3s;
  --color-dark:#2f2f2f;
  --color-light:#fff;
  --color-brand:#57bd84;
  --height:5rem;
  --btn-width:6rem;
  --bez:cubic-bezier(0,0,0.43,1.49);
 }
 body{
  background-color: #f5f5f5;
  background: var(--color-dark);
  height: 100vh;
  min-height: 100vh;
  display: grid;
  place-items: center;
 }
 .widget{
  margin: 35px 0;
  display: flex;
  height: 400px;
  width: 650px;
  flex-wrap: wrap;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0 27px 55px 0 rgba(0, 0, 0, 0.3);
 }
 .widget .weatherIcon{
  flex: 1 100%;
  height: 50%;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  background: #fafaff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 100px;
 }
 .weatherIcon{
   
padding-left: 35vh;
 }
 .widget .wetherInfo{
  flex: 0 0 70%;
  height: 40%;
  background: #080705;
  display: flex;
  align-items: center;
  color: white;
 }
 .widget .wetherInfo .temperature{
  flex: 0 0 40%;
  width: 100%;
   font-size: 65px;
   display: flex;
    
   justify-content: space-around;
   margin-right: 20px;
   
 }
 .widget .wetherInfo .description{
  flex: 0 60px;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: center;
 }
 .widget .wetherInfo .description .weatherCondition{
   text-transform: uppercase;
   font-size: 35px;
   font-weight: 100;
 }
 .widget .wetherInfo .description .place{
  font-size: 15px;
 }
 .widget .date{
  flex: 0 0 30%;
  height: 40%;
  background: #70c1b3;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: white;
  font-size: 30px;
  font-weight: 800;
  text-align: center;
 }
 .widget .extra-temp{
  flex: 1 100%;
  padding: 5% 0;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
  background: #fafafa;
  display: grid;
  grid-template-columns: repeat(2 ,1fr);
  align-items: center;
  font-size: 100%;
 }
 .widget .extra-temp p{
  font-size: 25px;
  font-weight: 500;
  
 }
 .extra-info-leftside{
  width: 19vh;
 
 }
  .extra-temp .temp-info-minmax {
  display: flex;
  justify-content:  space-between;
   
}
.two-sided-section{
  display: flex;
 
}
 

 .search{
  width: 100%;
  position: relative;
  display: flex;
  justify-content: center;
 }
 .searchTerm{
  width: 50%;
  border: none;
  padding: 16px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  font-size: 18px;
 }
 .searchTerm:focus{
  color: #00b4cc;
 }
 .searchButton{
  width: 80px;
  height: 52px;
  border: none;
  background: #018b9c;
  text-align: center;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
  outline: none;
 }
 .searchButton:hover{
  background-color: #17b8ce;
 }
 .wrap{
  display: flex;
  justify-content: center;
  text-align: center;
  margin-top: -150px;
  margin-bottom: 50px;
 }
 article div .abc{
  font-size: 75px;
  margin-right: 95px;
 }